'use strict';

define(['lib/request', 'lib/url'], function (req, url) {

    "use strict";

    var auth = {
        login: function login(username, password) {

            return req.post(url.resolveApi('/token'), 'grant_type=password&username=' + username + '&password=' + password, {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded'
                }
            });
        }
    };

    return auth;
});